			<image class="bgc-picture1" src="../../static/fristPage/bgcL.png" mode="widthFix"></image>
			<view class="" style="background-color: #2b95d0; position: fixed; bottom: 0; z-index: -2; height: 100vh; width: 750rpx;"></view>		
			<view class="" style="height: 290rpx;"></view> 
			<view v-if="subNoOkFlag" class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 848rpx;">
				<view  class="" style="position: absolute; top: 130rpx; left: 78rpx;"
				hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image @click="backRegisterss" src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
				</view>
				<view class="" style="height: 70rpx;"></view>
				<view class="f43rpx color-H0f649c ta-c fw800 lsp-5" style="">
					 Subscription Connection Failure
				</view>
				<view class="ta-c f33rpx color-H0f649c" style="margin: 40rpx 20rpx ;">
					Uh oh! Your subscription was not connected.
				</view>
				<view class="ta-c f33rpx color-H0f649c" style="margin: 0rpx 40rpx 20rpx;">
					<text class="fw800" style="margin-right: 10rpx;">Note:</text> 
					This may require a re-try so please click below to try again.
				</view>
				<view class="ta-c bgc-o-r FFF fw800 f33rpx" @click="goSubCon"
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
				style="border-radius: 42rpx; line-height: 84rpx; height: 84rpx; letter-spacing: 2rpx; margin: 0 45rpx 23rpx;">
					Re-Verify
				</view>
				<view class="ta-c f33rpx color-H0f649c" style="margin: 0rpx 40rpx 60rpx;">
					If you are still experiencing issues, contact us for assistance.
				</view>
				<view class="df-jcc-aic">
					<view class="ta-c f33rpx color-H0f649c fw800" @click="goContactUS" 
					hover-class="hover" hover-start-time=0 hover-stay-time=150
					style="border-bottom: 5rpx solid #0f649c; line-height: 33rpx;">
						Contact US
					</view>
				</view>
			</view>
			
			<view class="" v-else>
				<view  class="" style="position: absolute; top: 130rpx; left: 78rpx;"
				hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image @click="backRegister" src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
				</view>
				<view class="">
					<view class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 900rpx;">
						<view class="" style="height: 70rpx;"></view>
						<view class=" ta-c color-H0f649c fw800" style="font-size: 43rpx;"> Need Assistance?</view>
						<view class="ta-c color-H0f649c f33rpx" style="margin: 50rpx 50rpx 42rpx; line-height: 42rpx;">
							 If you need further assistance, 
							 please connect to our customer 
							 service team through phone or email.
						</view>
						<view class="ta-c color-H0f649c fw800 f40rpx" style="">
							Email:
						</view>
						<view class="ta-c f33rpx" 
						style="color: #eb5120; margin: 0 0 50rpx; line-height: 42rpx; letter-spacing: 2rpx;">{{email}}</view>
						<view class="ta-c color-H0f649c fw800 f40rpx" style="margin: ;">
							Call Number:
						</view>
						<view class="ta-c f33rpx" style="color: #eb5120; line-height: 42rpx; margin: 0rpx 0 50rpx;">
							{{phone}}</view>
						<view class="ta-c color-H0f649c f33rpx" 
						style="margin: 0rpx 34rpx 30rpx; line-height: 42rpx;">
							If youâ€™d like to back out, please click below.
						</view>
						<view class="FFF f33rpx fw800 ta-c bgc-o-r" @click="gosubscr"
						hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
						style="margin: 33rpx 43rpx 38rpx 48rpx; height: 80rpx; line-height: 80rpx; border-radius: 40rpx;"
						>Cancel
						</view>
					</view>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	export default{
		data(){
			return{
				subNoOkFlag:true,
				email:'',
				phone:'',
			}
		},
		created() {
			this.getDate()
		},
		methods:{
			async getDate(){
				let _this = this
				const res = await this.$myRequest({
					url:'/api/sub/conf'
				})
				console.log(res);
				if(res.data.succeeded){
					_this.email = res.data.data.email
					_this.phone = res.data.data.phone
				}
			},
			backRegister(){
				this.subNoOkFlag = true
			},
			async goSubCon(){
				this.PaymentFlag = false
				this.SunConFlag = true
				const res = await this.$myRequest({
					url:'/api/sub/validate'
				}) 
				console.log(res);
				if(res.data.succeeded){
					if(res.data.data.active){
						uni.reLaunch({
							url:'./subOk'
						})
					}else{
						this.$refs.subNoOK.openPopup('Your subscription was not connected')
					}
				}
			},
			 goContactUS(){
				this.subNoOkFlag = false
			 },
			 gosubscr(){
				 uni.reLaunch({
				 	url:'./subscription'
				 })
			 },
			 backRegisterss(){
				 uni.reLaunch({
				 	url:'./subscription'
				 })
			 }
		}
	}
</script>

<style scoped>
	.bgc-picture1{
		 z-index:-1; 
		 width:100%; 
		 height:100%; 
		 position: fixed;  
		 top:0; 
	}
</style>

